import { h, Component } from "preact"
import { connect } from "preact-redux"
import styled from "styled-components"

import Drawer from "./drawer"

class Aside extends Component {
  open = false
  startX = 0
  diffX = 0

  componentDidUpdate(prevProps) {
    if (prevProps.sidebarVisible !== this.props.sidebarVisible) {
      this.toggleDrawer()
    }
  }

  handleTouchEnd = () => {
    if (this.diffX > window.innerWidth * 0.3) {
      this.toggleDrawer()
      this.diffX = 0
    }
  }

  handleTouchStart = event => {
    this.startX = event.targetTouches[0].clientX
  }

  handleTouchMove = event => {
    const { clientX } = event.targetTouches[0]
    this.diffX = Math.abs(clientX - this.startX)
  }

  handleDrawer = node => {
    this.drawer = node
  }

  handleOverlay = node => {
    this.overlay = node
  }

  toggleDrawer = () => {
    if (this.open) {
      this.drawer.classList.toggle("active")
      this.overlay.classList.toggle("active")
    } else {
      this.drawer.classList.toggle("active")
      this.overlay.classList.toggle("active")
    }
    this.open = !this.open
  }

  render() {
    const events = {
      onTouchStart: this.handleTouchStart,
      onTouchMove: this.handleTouchMove,
      onTouchEnd: this.handleTouchEnd
    }

    return (
      <aside>
        <Drawer innerRef={this.handleDrawer} events={events} />
        <Overlay
          onClick={this.toggleDrawer}
          innerRef={this.handleOverlay}
          {...events}
        />
      </aside>
    )
  }
}

const Overlay = styled.div`
  position: fixed;
  top: 0px;
  left: 0px;
  width: 20px;
  height: 100%;
  display: var(--overlay);
  transition: background-color 300ms ease;
  z-index: 5;

  &.active {
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 50;
    width: 100%;
  }
`

const state = ({ root: { sidebarVisible } }) => ({
  sidebarVisible
})

export default connect(state)(Aside)



